.wrapper {
   margin: 0 auto;
   padding: 90px 0 70px;
   width: 470px;
}

@media screen and (min-width: 1280px) {
   .wrapper {
      padding: 180px 0 240px;
   }
}

.title {
   margin-bottom: 50px;
   text-align: center;
   font-family: var(--logo-font);
   font-weight: 400;
   font-size: 34px;
   line-height: 1.12;
}

.subtitle {
   margin-bottom: 15px;
   font-weight: 500;
   font-size: 20px;
   line-height: 1.9;
}

.list:nth-child(3) {
   margin-bottom: 30px;
}

.item {
   position: relative;
   display: flex;
   align-items: center;
   font-weight: 500;
   font-size: 14px;
   line-height: 1.21;
   color: var(--secondary-text-color);
}

.item:not(:last-child) {
   margin-bottom: 12px;
}

.item::before {
   position: absolute;
   content: '';
   top: 4px;
   left: -12px;
   height: 8px;
   width: 4px;
   background-image: url('images/arrow-right.svg');
   background-size: contain;
   background-repeat: no-repeat;
}